import {
	mapMutations,
	mapState
} from 'vuex';
export default {
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			smallAmount: 0,
			cldList: [{
				id: 0,
				image: '../../static/cld_small.jpg',
				title: '5KG装',
				price: 30,
				cldAmount: 0,
				cldSpec: "5",
			}, {
				id: 1,
				image: '../../static/cld_medium.jpg',
				title: '10KG装',
				price: 50,
				cldAmount: 0,
				cldSpec: "10",
			}, {
				id: 2,
				image: '../../static/cld_large.jpg',
				title: '15KG装',
				price: 80,
				cldAmount: 0,
				cldSpec: "15",
			}],
			orderItemList: [],
			// 总价
			fee: 0,
			// 数量
			amount: 0,
			isCreateOrder: false,
		};
	},
	computed: {
		...mapState(['hasLogin'])
	},
	onLoad() {
		this.loadData();
	},
	methods: {
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		...mapMutations(['setOrderAmount', 'setOrderFee', 'setOrderItemList']),
		// 点击数量变化, number 数量，id 第几个
		numberChange(cldAmount, id) {
			this.orderItemList = [];
			this.cldList[id].cldAmount = parseInt(cldAmount);
			this.calcTotalFee();
		},
		navToLogin() {
			uni.navigateTo({
				url: '../public/login',
			})
		},
		// 计算总价,钢瓶个数
		calcTotalFee() {
			var totalFee = 0;
			var totalItem = 0;
			this.cldList.forEach(item => {
				var tempItemFee = item.cldAmount * item.price;
				totalFee = totalFee + tempItemFee;
				totalItem = parseInt(item.cldAmount) + totalItem;
			});
			this.cldList.forEach(item => {
				if (item.cldAmount != 0) {
					this.orderItemList.push(item);
				}
			});
			this.fee = totalFee;
			this.amount = totalItem;
			// this.amount != 0 ? this.isCreateOrder = true : this.isCreateOrder = false
			if (this.amount != 0) {
				this.isCreateOrder = true;
			} else {
				this.isCreateOrder = false;
			}
		},
		// 去订单页面
		createOrder() {
			this.setOrderItemList(this.orderItemList);
			this.setOrderAmount(this.amount);
			this.setOrderFee(this.fee);
			this.orderItemList = [];
		},
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		async loadData() {
			let carouselList = await this.$api.json('carouselList');
			this.titleNViewBackground = carouselList[0].background;
			this.swiperLength = carouselList.length;
			this.carouselList = carouselList;

			let goodsList = await this.$api.json('goodsList');
			this.goodsList = goodsList || [];
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
	},

























}
